.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.ant-layout{font-family:'Overpass', sans-serif}.ant-card{border-radius:12px !important}.header-custom{color:#f0f2f5 !important;font-family:'Kosugi Maru', sans-serif}.menu-mobile{position:absolute;color:white;right:0;top:0;padding:20px;font-size:25px}.banner-img{height:100%;width:100%}.banner-text{position:absolute;top:40%;left:50px;font-family:'Overpass', sans-serif;-webkit-flex-direction:column !important;flex-direction:column !important}.banner-text p{margin:0}.banner-text p.hello{font-size:3rem;font-weight:900;text-transform:uppercase;color:#ea4c89}.banner-text p.name{font-size:3rem;font-weight:900;text-transform:uppercase;color:#f0f2f5}.banner-text p.position{font-size:5rem;font-weight:900;text-transform:uppercase;color:#f0f2f5}.banner-text-mobile{position:absolute;top:10%;font-family:'Overpass', sans-serif}.banner-text-mobile p{margin:0}.banner-text-mobile p.hello{font-size:20px;font-weight:900;text-transform:uppercase;color:#ea4c89}.banner-text-mobile p.name{font-size:20px;font-weight:900;text-transform:uppercase;color:#f0f2f5}.banner-text-mobile p.position{font-size:25px;font-weight:900;text-transform:uppercase;color:#f0f2f5}.btn-banner .ant-btn.ant-btn-lg{padding:2px 20px !important}.ant-btn.ant-btn-lg.btn-sns{padding:2px 10.5px !important}.ant-btn.btn-green{font-weight:700;background-color:#00e676;border-color:#00e676;color:#1b1b1b}.ant-btn.btn-green:hover,.ant-btn.btn-green:focus,.ant-btn.btn-green:active{font-weight:700;background-color:#69f0ae;border-color:#69f0ae;color:#1b1b1b}.ant-btn.btn-pink{font-weight:700;background-color:#ea4c89;border-color:#ea4c89;color:#f0f2f5}.ant-btn.btn-pink:hover,.ant-btn.btn-pink:focus,.ant-btn.btn-pink:active{font-weight:700;background-color:#e76798;border-color:#e76798;color:#f0f2f5}.ant-typography.description-value{font-size:16px}.custom-modal .ant-modal-content{border-radius:10px}.custom-modal .ant-modal-content .ant-modal-header{border-radius:10px}.custom-modal .ant-modal-content .ant-tag{color:white;background:#40a9ff;border-radius:20px;padding:5px 10px;font-weight:500;border:none;margin-top:10px}.list-project .ant-list{width:100%}.logo{width:80px;position:absolute;margin:10px}.site-back-top-basic{color:rgba(64,64,64,0.6)}.list-menu{position:absolute;right:20px;top:20px}.list-menu .ant-typography>a{color:#f0f2f5;padding:0px 20px;font-weight:600;font-size:15px}.list-menu .ant-typography>a:hover{color:#c7c7c7;padding:0px 20px;font-weight:600;font-size:15px}.list-menu-mobile{position:absolute;right:10px;top:15px;color:#f0f2f5;font-size:25px}.ant-popover-inner-content .ant-row.menu-item-mobile>.ant-typography>a{color:#ea4c89;font-weight:600;font-size:15px}.ant-popover-inner-content .ant-row.menu-item-mobile>.ant-typography>a:hover{color:#e76798;font-weight:600;font-size:15px}

